

import http from '@/http/interface.js'
export default {
	onLaunch: function() {
		console.log('App Launch')
		//检查app版本
		
		
		//校验登录状态
		let _this = this;
		uni.getStorage({
			key : "userLoginInfo",
			success: function (res) {
				_this.$store.commit('userIsLogin',res.data);
				uni.redirectTo({
					url : '/pages/main/main',
				})
			},
			fail: function(){
				console.error('登录失效');
				uni.reLaunch({
					url: '/pages/checkpoint/login/login?msg=登录失效'
				});
			}
		});
	},
	onShow: function() {
		console.log('App Show');
	},
	onHide: function() {
		console.log('App Hide')
	},
	mounted() {
		
	}
}
